<template>
  <main class="catalog">
    <section class="catalog__product">
      <div class="product">
        <preloader v-if="productsLoading"/>
        <ProductList :products="products"/>
      </div>
    </section>
    <CatalogInfo/>
  </main>
</template>
<script>
import ProductList from '@/components/product/ProductList'
import Preloader from '@/components/common/Preloader'
import CatalogInfo from '@/components/catalog/CatalogInfo'
import { mapActions } from 'vuex'

export default {
  name: 'MainPage',
  components: {
    ProductList,
    Preloader,
    CatalogInfo
  },
  data () {
    return {
      productsLoading: false
    }
  },
  computed: {
    products () {
      return this.$store.state.products
    }
  },
  methods: {
    ...mapActions(['loadProducts']),
    getProductsList () {
      if (this.products.length < 1) this.productsLoading = true
      this.loadProducts().then(() => this.productsLoading = false) //eslint-disable-line
    }
  },
  created () {
    this.getProductsList()
  }
}
</script>
<style scoped lang="scss">
.catalog {
  display: flex;
  position: relative;
  @include respond-to(lg) {
    overflow-y: hidden;
  }

  &__product {
    position: relative;
    flex: 1;
  }
}

.failed {
  position: absolute;
  top: 30px;
}
</style>
